
.energy .card-item .card-title {
    font-size: 18px;
    font-weight: 700;
}

.energy .card-item .chart-area {
    margin-top: 10px;
    width: 100%;
    height: 300px;
    background-color: #eee;
}

.energy .bottom-card-row .card-item .card-title {
    margin-bottom: 20px;
}
.energy .bottom-card-row .card-item .card-content {
    border-top: 1px solid #eee;
    padding: 20px 0px;
    display: flex;
    justify-content: space-around;
}
.energy .bottom-card-row .card-item .card-content .summary-item .summary-value {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 10px;
}
.energy .bottom-card-row .card-item .card-content .summary-item.blue .summary-value{
    color: #3366FF;
}
.energy .bottom-card-row .card-item .card-content .summary-item.green .summary-value{
    color: #0f7d12;
}
.energy .bottom-card-row .card-item .card-content .summary-item.pink .summary-value{
    color: #E91E63;
}