.page-wrapper .main-page .main-page-inner.canvas {padding: 0;    width: 100%; height: 100%;}
.page-wrapper .main-page .main-page-inner.canvas.active{z-index: 2;}

.container { flex: 1; height: 100%; background-color: grey; }

.tool-bar {background-color: #FFFFFFDD; height: 100%; border-right: 1px solid #00000088;}

.canvas .tab-wrapper {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
    display: flex;
    background-color: #FFFFFFDD;
    height: 60px;
    border-bottom: 1px solid #00000088;
}
.canvas .tab-wrapper .tab-item{
    flex: 1;
    border-left: 1px solid #00000022;
    border-right: 1px solid #00000022;
    transition: all 0.2s;
    cursor: pointer;
}
.canvas .tab-wrapper .tab-item:hover {
    background-color: #AACCFF44;
}
.canvas .tab-wrapper .tab-item.active {
    background-color: #AACCFF88;
    color: dodgerblue;
    font-weight: 700;
}
.canvas .tab-wrapper .tab-item.active i {
    color: dodgerblue;
}
.canvas .setting {
    position: absolute; top: 60px; left: 0; width: 300px; height: calc(100% - 60px); background-color: #FFFFFFDD; border-bottom: 1px solid #00000088; transition: all 0.3s; transform: translateX(-100%);
}
.canvas .setting.show { transform: translateX(0);}
.canvas .setting * {color:black;}
.canvas .setting-wrapper {position: relative; width: 100%; padding: 10px 0px; height: calc(100% - 10px);}
.canvas .setting .setting-arrow { position: absolute; top: 0px; right: -30px; width: 30px; height: 60px; transition: all 0.3s; background-color: #FFFFFFDD; cursor: pointer; z-index: 1;}
.canvas .setting.show .setting-arrow { right: 0px;}

.canvas .setting-content {
    width: calc(100% - 0px);
    padding-right: 0px;
    height: calc(100% - 10px);
    overflow-y: auto;
}
.canvas .setting-content .title {
    margin-top: 10px;
    text-align: center;
    font-size: 20px;
}
.canvas .setting-content .setting-part {
    position: relative;
    border-top: 1px solid #00000033;
    padding: 20px 0px;
}
.canvas .setting-content .setting-part .top-button {
    position: absolute;
    top: 25px;
    right: 10px;
    height: 30px;
    font-size: 14px;
    line-height: 30px;
    padding: 0px 10px;
}
/* .canvas .setting-content .setting-part .setting-inner {

} */
.page-wrapper .canvas .setting-content .setting-part .sub-title {
    margin: 0px;
    padding: 12px 10px;
    font-size: 20px;
    font-weight: 700;
    font-size: 18px;
    color: #226;
}
.canvas .setting-content .setting-part .del-btn {
    position: absolute;
    right: 10px;
    top: 0px;
    padding: 5px 15px;
    height: auto;
}
