.canvas .setting-content .sub-title.flag-title:hover { background-color: #BBFFEE66; color: #3366FF; } */
.canvas .set-item {margin: 10px; display: flex; align-items: center;}
.canvas .set-item .label {width: 90px; font-size: 16px;}
.canvas .set-item .content {flex: 1; margin: 0px 10px;}
.canvas .set-item .content input {width: 100%;}
.canvas .set-item .value {width: 55px; text-align: right; margin-right: 5px;}
.canvas .set-item .value input {width: 100%;}
