.canvas .set-item.analyze-obst-item {
    padding: 10px;
    background-color: #00000022;
    border-radius: 4px;
}
.canvas .set-item .create-button .tool-tip {
    opacity: 0;
    transition: all 0.1s;
}
.canvas .set-item .create-button:hover .tool-tip {
    opacity: 1;
}
