
.dashboard .top-card-row .card-item {
    display: flex;
    align-items: center;
}
.dashboard .top-card-row .card-item .icon-side {
    margin-right: 12px;
    height: 40px;
    width: 40px;
    border-radius: 5px;
}
.dashboard .top-card-row .card-item .icon-side i {
    font-size: 24px;
}
.dashboard .card-item .blue, .dashboard .card-item .pending {
    background-color: #3366FF33;
}
.dashboard .card-item .green, .dashboard .card-item .complete {
    background-color: #4CAF5033;
}
.dashboard .card-item .orange, .dashboard .card-item .progress {
    background-color: #FF980033;
}
.dashboard .card-item .pink, .dashboard .card-item .error {
    background-color: #E91E6333;
}
.dashboard .card-item .blue i, .dashboard .card-item .pending {
    color: #3366FF;
}
.dashboard .card-item .green i, .dashboard .card-item .complete {
    color: #0f7d12;
}
.dashboard .card-item .orange i, .dashboard .card-item .progress {
    color: #FF9800;
}
.dashboard .card-item .pink i, .dashboard .card-item .error {
    color: #E91E63;
}

.dashboard .top-card-row .card-item .label-side {
    flex: 1;
}
.dashboard .top-card-row .card-item .label-side .card-value {
    font-size: 20px;
    font-weight: bold;
}
.dashboard .top-card-row .card-item .label-side .card-title {
    font-size: 16px;
    color: grey;
}

.dashboard .bottom-card-row .card-item {
    flex: 1;
    padding: 0;
    background-color: white;
}
.dashboard .bottom-card-row .card-item .card-title {
    padding: 20px 15px;
    font-size: 20px;
    font-weight: 700;
    border-bottom: 1px solid #eee;
}
.dashboard .bottom-card-row .card-item .card-content {
    padding: 20px 15px;
    height: 275px;
    overflow-y: auto;
    margin-bottom: 20px;
}
.dashboard .bottom-card-row .card-item .card-content .project-item {
    border-radius: 5px;
    padding: 20px 16px;
    width: calc(100% - 32px);
    background-color: #F6F6F6;
    display: flex;
    margin: 20px 0px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    cursor: pointer;
    transition: all 0.3s;
}
.dashboard .bottom-card-row .card-item .card-content .project-item:hover {
    transform: translateY(-5px);
    background-color: #E0E0E0;
}
.dashboard .bottom-card-row .card-item .card-content .project-item .project-icon {
    width: 40px;
    height: 40px;
    border-radius: 5px;
    margin-right: 12px;
}
.dashboard .bottom-card-row .card-item .card-content .project-item .project-icon i{
    font-size: 22px;
}
.dashboard .bottom-card-row .card-item .card-content .project-item .project-info {
    flex: 1;
}

.dashboard .bottom-card-row .card-item .card-content .project-item .project-info .project-title {
    font-weight: bold;
}

.dashboard .bottom-card-row .card-item .card-content .project-item .project-info .project-date {
    color: #888;
    font-size: 14px;
}
.dashboard .bottom-card-row .card-item .card-content .project-item .project-status {
    text-transform: capitalize;
    padding: 5px 10px;
    border-radius: 4px;
    font-size: 14px;
}

.dashboard .bottom-card-row .action-row {
    display: flex;
    gap: 20px;
    margin: 20px 0px;
}
.dashboard .bottom-card-row .action-item {
    flex: 1;
    padding: 20px;
    border-radius: 5px;
    transition: all 0.3s;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}
.dashboard .bottom-card-row .action-item:hover {
    transform: translateY(-5px);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
}
.dashboard .bottom-card-row .action-item .action-icon {
    margin-bottom: 10px;
}
.dashboard .bottom-card-row .action-item .action-title {
    font-size: 18px;
    font-weight: 700;
}
.dashboard .bottom-card-row .action-item .action-label {
    font-size: 14px;
    color: #888;
}