
.menu-wrapper {
    padding: 20px;
}
.menu-wrapper .menu-item {
    display: flex;
    align-items: center;
    padding: 20px;
    width: calc(100% - 40px);
    /* margin: 5px 0px; */
    border-bottom: 1px solid #EEE;
    border-radius: 4px;
    transition: all 0.2s;
    cursor: pointer;
}
.menu-wrapper .menu-item i {
    font-size: 20px;
    margin-right: 10px;
    color: grey;
}
.menu-wrapper .menu-item label {
    font-size: 16px;
    font-weight: 500;
}

.menu-wrapper .menu-item:hover {
    background-color: #F0F0FF;
}

.menu-wrapper .menu-item.active {
    background-color: #AACCFF88;
}
.menu-wrapper .menu-item.active label {
    color: dodgerblue;
}
.menu-wrapper .menu-item.active i {
    color: dodgerblue;
}
