
.project .card-wrapper {
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    gap: 20px; /* spacing between cards */
    justify-content: center;
    max-width: 100%;
}

.project .card-wrapper .card-item {
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0,0,0,0.1);
    padding: 20px;
    min-width: calc(400px - 40px);
    max-width: 500px;
    /* transition: all 0.3s;
    cursor: pointer; */
}
/* .project .card-wrapper .card-item:hover {
    transform: translateY(-5px);
    background-color: #eef;
    box-shadow: 0 6px 12px rgba(0,0,0,0.2);
} */

.project .card-wrapper .card-item .title-row {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.project .card-wrapper .card-item .title-row .card-title {
    flex: 1;
    font-size: 18px;
    font-weight: 700;
}

.project .card-wrapper .card-item .title-row .card-status {
    padding: 5px 10px;
    border-radius: 5px;
}

.project .card-item .card-status {
    padding: 6px 10px;
    border-radius: 5px;
    text-transform: capitalize;
}
.project .card-item.pending .card-status {
    background-color: #3366FF33;
    color: #3366FF;
}
.project .card-item.active .card-status {
    background-color: #0f7d1233;
    color: #0f7d12;
}
.project .card-item.progress .card-status {
    background-color: #FF980033;
    color: #FF9800;
}
.project .card-item.error .card-status {
    background-color: #E91E6333;
    color: #E91E63;
}

.project .card-wrapper .card-item .card-description {
    margin: 10px 0px;
    font-size: 14px;
    color: #666;
}
.project .card-wrapper .card-item .summary-item {
    display: flex;
    align-items: center;
    margin: 10px 0px;
}

.project .card-wrapper .card-item .summary-item .summary-label {
    flex: 1;
}
.project .card-wrapper .card-item .summary-item .summary-value {
    font-weight: 700;
}

.project .card-item.pending .color-value {
    color: #3366FF;
}
.project .card-item.active .color-value {
    color: #0f7d12;
}
.project .card-item.progress .color-value {
    color: #FF9800;
}
.project .card-item.error .color-value {
    color: #E91E63;
}
.project .card-item .button {
    width: calc(100% - 30px);
    cursor: pointer;
    background-color: #3366FF;
    color: white;
    transition: all 0.3s;
}
.project .card-item .button:hover {
    background-color: #0056b3;
}

.project .create-card-item {
    height: 200px;
    justify-content: space-around;
}
.project .create-card-item .card-title {
    font-size: 18px;
    font-weight: 700;
}
.project .create-card-item .icon-wrapper {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #DDD;
    color: #888;
    font-size: 30px;
}